$main-color: #f44336;
$main-color: #333;
$secondary-color: #0599be;
$width: 550px; // Change Me

* {
  box-sizing: border-box;
}

body {
  background: $main-color;
}

.feeling__container {
  width: $width;
  height: fit-content;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.feeling__wrapper {
  width: 100%;
  height: calc($width/5);
  overflow: hidden;
  position: relative;
}

label {
  position: absolute;
  top: calc($width/15);
  color: rgba(white, 0.5);
  font-family: Roboto;
  font-weight: 400;
  cursor: text;
  transition: 0.25s ease;
}

.feeling__input {
  display: block;
  width: 100%;
  padding-top: calc($width/15);
  border: none;
  border-radius: 0; // For iOS
  // border-bottom: solid $width/150 rgba(white, .5);
  color: white;
  background: $main-color;
  font-size: calc($width/15);
  transition: 0.3s ease;
  &:valid {
    // border-bottom-color: rgba(white, .5);
    ~ label {
      top: 0;
      font-family: Roboto;
      font-weight: 700;
      color: rgba(white, 0.5);
      // background-color: red;
    }
  }
  &:focus {
    outline: none;
    // border-bottom-color: $secondary-color;
    ~ label {
      top: 0;
      font-family: Roboto;
      font-weight: 700;
      color: $secondary-color;
    }

    ~ .bar:before {
      transform: translateX(0);
    }
  }

  // Stop Chrome's hideous pale yellow background on auto-fill

  &:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 1000px $main-color inset;
    -webkit-text-fill-color: white !important;
    // border-bottom-color: rgba(white, .5);
  }
}

.bar {
  // background: $secondary-color;
  background: rgba(white, 0.5);
  content: "";
  width: $width;
  // height: $width/100;
  height: calc($width/150);
  // transform: translateX(-100%);
  transition: 0.3s ease;
  // margin-top: -2px;
  //
  position: relative;
  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 150%;
    background: $secondary-color;
    transform: translateX(-100%);
  }
}

.feeling__btn {
  appearance: button;
  background-color: #f0f8fa;
  // background-image: linear-gradient(to bottom, #fff, #f8eedb);
  border: 0 solid #e5e7eb;
  // border-radius: 0.5rem;
  box-sizing: border-box;
  color: #482307;
  display: flex;
  font-family: ui-sans-serif, system-ui, -apple-system, system-ui, "Segoe UI",
    Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 100%;
  font-weight: 700;
  margin: 0;
  outline: 2px solid transparent;
  padding: 1rem 1.5rem;
  text-align: center;
  text-transform: none;
  transition: all 0.1s cubic-bezier(0.4, 0, 0.2, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  box-shadow: -6px 8px 10px rgb(81 41 10 / 10%), 0px 2px 2px rgb(81 41 10 / 20%);

  &:active {
    box-shadow: #0599be 0 0 0 4px, -6px 8px 10px #51290a1a,
      0px 2px 2px #51290a33;
  }

  &:hover {
    box-shadow: #0599be 0 2px 8px;
    opacity: 0.85;
  }
}

.feeling__result {
  color: white;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  margin-top: 3rem;
}

::selection {
  background: rgba($secondary-color, 0.3);
}
